body {
  background: #A1A5A7;
}

.container {
  width: 400px;
  margin: 100px auto;
  text-align: center;
}

button {
  border: none;
  outline: none;
  background: black;
  color: #282828;
  font-size: 1em;
  text-align: center;
  color: white;
}
button:active {
  box-shadow: 0px 0px 5px black;
}

.start {
  width: 150px;
  height: 50px;
}
.size1 {
  width: 150px;
  height: 50px;
  border-radius: 10%;
}
.size2 {
  width: 160px;
  height: 60px;
  border-radius: 20%;
}
.size3 {
  width: 170px;
  height: 70px;
  border-radius: 30%;
}
.size4 {
  width: 180px;
  height: 80px;
  border-radius: 40%;
}
.size5 {
  width: 190px;
  height: 90px;
  border-radius: 50%;
}
.size6 {
  width: 100px;
  height: 100px;
  border-radius: 60%;
  background: #3c0000;
}
.size7 {
  width: 110px;
  height: 110px;
  border-radius: 70%;
  background: #600000;
}
.size8 {
  width: 120px;
  height: 120px;
  border-radius: 80%;
  background: #810000;
}
.size9 {
  width: 130px;
  height: 130px;
  border-radius: 90%;
  background: #b60000;
}
.size10 {
  width: 140px;
  height: 140px;
  border-radius: 100%;
  background: #d00000;
}
.size11 {
  width: 300px;
  height: 300px;
  border-radius: 100%;
  color: black;
  font-size: 3em;
  background: white;
}

